import { BaseValueObject, StringOrNull } from '../base';

export class ImagePath extends BaseValueObject<'ImagePath', StringOrNull> {
  static from(value?: StringOrNull): ImagePath {
    return new ImagePath(value ?? null);
  }

  protected validate(value: StringOrNull): StringOrNull {
    if (value) {
      const trimmed = value.trim();

      if (trimmed.length > 400) {
        throw new Error('ImagePath has maximum length of 400');
      }
    }
    return value;
  }
}
